import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const headerSkeletonStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                --loader-content-gap: 0px;
            }

            ${root} {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
            }

            ${child('top')} {
                display: flex;
                padding-left: var(--header-skeleton-padding, 24px);
                padding-right: var(--header-skeleton-padding, 24px);
                flex-direction: row;
                align-items: center;
                width: 100%;
                height: 48px;
                gap: 16px;
            }

            ${child('left')} {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;
                height: 100%;
                flex: 1;
                overflow: hidden;
            }

            ${child('right')} {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-end;
                gap: 16px;
                flex: 1;
            }

            ${is('loaded')} ${child('left')} {
                flex: 1 1 auto;
            }

            ${is('loaded')} ${child('right')} {
                flex: 0 1 0;
            }

            ${child('left-loader')} {
                --loader-display: flex;
                --loader-align: center;
                flex-direction: row;
                --loader-content-align: center;
                height: 24px;
                --loader-justify: flex-start;
            }

            ${child('right-loader')} {
                display: flex;
                --loader-display: flex;
                --loader-align: center;
                --loader-content-align: center;
                --loader-justify: flex-end;
                --loader-content-gap: 16px;
            }

            ${is('!loaded')} ${child('left-loader')} {
                width: 50%;
            }

            ${is('!loaded')} ${child('right-loader')} {
                width: 50%;
            }

            ${child('left-panel-loader')} {
                flex: 1 1 auto;
                overflow: hidden;
            }

            ${child('right-panel-loader')} {
                flex: 0 1 0;
                --loader-display: flex;
                --loader-align: center;
                --loader-content-align: center;
                --loader-justify: flex-end;
            }

            ${child('title')} {
                font-size: 18px;
                white-space: nowrap;
                margin: 0;
            }

            ${is('subtitle')} ${child('sub-text')} {
                padding: 0 16px;
            }

            ${child('sub-text')} {
                white-space: nowrap;
                font-size: 14px;
                margin: 0;
                color: ${cssVariableAsVar('color_secondary_8')};
            }

            ::slotted([slot='icon-slot']) {
                padding-right: 8px;
                font-size: 24px;
            }

            ::slotted([slot='right-slot']) {
                display: flex;
                flex-direction: row;
                gap: 16px;
            }

            ${child('panel')} {
                padding-left: var(--header-skeleton-panel-padding, var(--header-skeleton-padding, 24px));
                padding-right: var(--header-skeleton-panel-padding, var(--header-skeleton-padding, 24px));
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                width: 100%;
            }

            ::slotted([slot='panel-right']) {
                height: 40px;
            }

            ::slotted([slot='panel-left']) {
                height: 40px;
            }
        `,
    ];
};
