import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { headerSkeletonStyles } from './headerSkeletonStyle';
import { HeaderSkeletonProperties, HeaderSkeletonSlot } from './headerSkeletonProperties';

@defineElement({ tagName: 'header-skeleton', styles: headerSkeletonStyles, version })
export class HeaderSkeleton extends BaseElement implements HeaderSkeletonProperties {
    // Static members
    private static InlineEdit;
    private static Loader;
    private static SkeletonRectangle;
    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: true, attribute: 'header-title' })
    headerTitle = '';
    @property({ type: String, reflect: true, attribute: 'sub-text' })
    subText = '';
    @property({ type: Boolean, converter: booleanConverter, reflect: true, attribute: 'inline-edit-title' })
    inlineEditTitle = false;
    @property({ type: Boolean, converter: booleanConverter })
    loaded = true;

    connectedCallback() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-inline-edit'), elem: ['InlineEdit'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-loader'), elem: ['Loader'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-skeleton'), elem: ['SkeletonRectangle'] });
        HeaderSkeleton.dynSubRegister(imports, () => {
            super.connectedCallback();
            this._handleInlineEdit = this._handleInlineEdit.bind(this);
            this.addEventListener('inline-edit-toggle', this._handleInlineEdit);
            this.addEventListener('inline-edit-save', this._handleInlineEdit);
        });
    }
    disconnectedCallback() {
        this.removeEventListener('inline-edit-toggle', this._handleInlineEdit);
        this.removeEventListener('inline-edit-save', this._handleInlineEdit);
        super.disconnectedCallback();
    }
    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            subtitle: this.subText !== '',
            loaded: this.loaded,
        });
        const titleHtml = this.inlineEditTitle
            ? html`<${HeaderSkeleton.InlineEdit.tag} class="${baseClassName}-inline" value=${this.headerTitle} header></${HeaderSkeleton.InlineEdit.tag}>`
            : html` <p class="${baseClassName}-title">${this.headerTitle}</p>`;

        this.style.containerType = 'inline-size';
        this.style.containerName = 'small';

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-top">
                    <div class="${baseClassName}-left">
                        <${HeaderSkeleton.Loader.tag} loaded="${this.loaded}" class="${baseClassName}-left-loader">
                            <${HeaderSkeleton.SkeletonRectangle.tag} width="100%" height="24px" slot="loader-slot"></${HeaderSkeleton.SkeletonRectangle.tag}>
                            <slot name="${HeaderSkeletonSlot.ICON_SLOT}" class="${baseClassName}-icon"></slot>
                            <slot name="${HeaderSkeletonSlot.INLINE_EDIT_SLOT}"></slot>
                            ${titleHtml}
                            <p class="${baseClassName}-sub-text">${this.subText}</p>
                            <slot name="${HeaderSkeletonSlot.LEFT_SLOT}"></slot>
                        </${HeaderSkeleton.Loader.tag}>
                    </div>
                    <div class="${baseClassName}-right">
                    <${HeaderSkeleton.Loader.tag} loaded="${this.loaded}" class="${baseClassName}-right-loader">
                            <${HeaderSkeleton.SkeletonRectangle.tag} width="100%" height="24px" slot="loader-slot"></${HeaderSkeleton.SkeletonRectangle.tag}>
                    <slot name="${HeaderSkeletonSlot.RIGHT_SLOT}"></slot>
                    </${HeaderSkeleton.Loader.tag}>
                </div>
                </div>
                <div class="${baseClassName}-panel">
                    <${HeaderSkeleton.Loader.tag} loaded="${this.loaded}" class="${baseClassName}-left-panel-loader">
                        <slot name="${HeaderSkeletonSlot.PANEL_LEFT}"></slot>
                    </${HeaderSkeleton.Loader.tag}>
                    <${HeaderSkeleton.Loader.tag} loaded="${this.loaded}" class="${baseClassName}-right-panel-loader">
                        <slot name="${HeaderSkeletonSlot.PANEL_RIGHT}"></slot>
                    </${HeaderSkeleton.Loader.tag}>
                </div>
            </div>
        `;
    }

    private _handleInlineEdit(e) {
        const details = e.detail;
        const right = this.shadowRoot!.querySelector('p[class$="-sub-text"]') as HTMLDivElement;
        if (details.open) {
            right.style.width = '100%';
            requestAnimationFrame(() => {
                right.style.whiteSpace = 'nowrap';
                right.style.transition = 'width 0.15s';
                right.style.width = '0px';
                right.style.padding = '0';
                right.style.overflow = 'hidden';
            });
        } else {
            right.style.width = 'initial';
            right.style.overflow = 'unset';
            right.style.padding = '0px 16px';
        }
    }
}
